<i18n>
{
  "en": {
    "ShopDetails": "ShopDetails",
    "Time": "BusinessHours",
    "ConsumptionPerPerson": "Consumption per person",
    "ShopAddress": "Shop address",
    "address": "Zhongshan avenue tangxia",
    "m": "{count} m",
    "Scanning": "Scanning two-dimensional code charging",
    "Charger": "Charger",
    "LeasedChargingTreasure": "Leased charging treasure",
    "Borrow": "Borrow {count}",
    "Repay": "Repay {count}",
    "Amap": "Amap",
    "BaiduMaps": "Baidu Maps",
    "Cancel": "Cancel"
  },
  "zh": {
    "ShopDetails": "店铺详情",
    "Time": "营业时间",
    "ConsumptionPerPerson": "人均消费",
    "ShopAddress": "店铺地址",
    "address": "中山大道棠下",
    "m": "{count}米",
    "Scanning": "扫码充电",
    "Charger": "提供座充",
    "LeasedChargingTreasure": "租借充电宝",
    "Borrow": "可借{count}台",
    "Repay": "可还{count}台",
    "Amap": "高德地图",
    "BaiduMaps": "百度地图",
    "Cancel": "取消"
  }
}
</i18n>
<template>
  <div class="store-details">
    <head-top :title='$t("ShopDetails")' :go-back="true" @goBackHander="goBackHander"></head-top>
    <div class="container">
      <div class="pic">
        <span></span>
      </div>
      <div class="details bomPixel">
        <ul class="left">
          <li>
            <img src="../../assets/store_images/time-icon.png" alt="">
            <span>{{ $t("Time") }}</span>
            <strong>10:00-23:00</strong>
          </li>
          <li>
            <img src="../../assets/store_images/user.png" alt="">
            <span>{{ $t("ConsumptionPerPerson") }}</span>
            <strong>￥50</strong>
          </li>
          <li>
            <img src="../../assets/store_images/add-icon.png" alt="">
            <span>{{ $t("ShopAddress") }}</span>
            <strong>{{ $t("address") }}</strong>
          </li>
        </ul>
        <div class="right">
          <div class="navigation" @click="navigation">
            <img src="../../assets/store_images/nav.png" alt="">
            <span>{{ $t("m",{count: 110}) }}</span>
          </div>
        </div>
      </div>
      <div class="column">
        <div class="charger">
          <img class="line" src="../../assets/store_images/line.png" alt="">
          <span>{{ $t("Scanning") }}</span>
          <strong>
            <img src="../../assets/store_images/charger-default.png" alt="">
            <b>{{ $t("Charger") }}</b>
          </strong>
        </div>
        <div class="power">
          <img class="line" src="../../assets/store_images/line.png" alt="">
          <span>{{ $t("LeasedChargingTreasure") }}</span>
          <strong>
            <img src="../../assets/store_images/baby.png" alt="">
            <b>{{ $t("Borrow",{count: 3}) }} {{ $t("Repay",{count: 3}) }}</b>
          </strong>
        </div>
      </div>
    </div>

    <!-- 操作表 -->
    <action-sheet :actionSheet="actionSheet"
                  :index="1"
                  :title="$t('address')"
                  :list="[$t('Amap'), $t('BaiduMaps')]"
                  :cancel="$t('Cancel')"
                  color="#4FA3FE"
                  @sheetHandle="sheetHandle"></action-sheet>
  </div>
</template>

<script>
import headTop from '../../components/Header/index'
import actionSheet from '../../components/ActionSheet/index'
export default {
  name: 'store-details',
  data () {
    return {
      actionSheet: false // 导航
    }
  },

  components: {
    headTop,
    actionSheet
  },

  methods: {
    goBackHander () {
      // console.log(123)
    },

    // 导航
    navigation () {
      this.actionSheet = true
    },

    sheetHandle (confirm, value, index) {
      this.actionSheet = false
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.store-details{
  .fs;
  background: #FFF;
  .container{
    .fs-noTop;
    font-size: 26px;
    .pic{
      .wh(690px, 400px);
      margin: 30px auto;
      span{
        .wh(100%, 100%);
        display: block;
        background: #CCC;
        border-radius: 20px;
      }
    }
    .details{
      .wh(100%, 230px);
      position: relative;
      .left{
        .wh-f(76%, 100%);
        text-align: left;
        padding-left: 4%;
        li{
          .wh(100%, 75px);
          img{
            height: 32px;
            position: relative;
            top: 10px;
          }
          span{
            color: #585858;
            margin: 0 10px 0 20px;
          }
          strong{
            color: #ABABAB;
            font-size: 30px;
          }
        }
        li:nth-child(3) {
          strong{
            font-size: 26px;
          }
        }
      }
      .right{
        .wh-f(24%, 100%);
        position: relative;
        .navigation{
          .wh(160px, 120px);
          .f-center;
          img{
            height: 60px;
            .f-center;
            top: 30%;
          }
          span{
            .f-center;
            top: 70%;
          }
        }
      }
    }
    .column{
      .wh(100%, auto);
      text-align: left;
      .charger, .power{
        .wh-f(auto, 146px);
        position: relative;
        .line{
          height: 70px;
          position: absolute;
          left: 28px;
          top: 50%;
          margin-top: -35px;
        }
        span{
          .wh(100%, 50%);
          line-height: 90px;
          display: block;
          font-size: 30px;
          padding-left: 70px;
        }
        strong{
          .wh(100%, 50%);
          line-height: 50px;
          display: block;
          padding-left: 70px;
          img{
            height: 40px;
            position: relative;
            top: 10px;
            left: 0;
          }
          b{
            margin-left: 8px;
            color: #AAAAAA;
          }
        }
      }
      .charger{
        margin-right: 40px;
      }
    }
  }
}
</style>



